import React, { Suspense, lazy } from "react";
import "./App.css";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import Navbar from "./components/Common_Elements/Navbar/Navbar";
import Home from "./components/Home/Home";
import Footer from "./components/Common_Elements/Footer/Footer";
import Spinner from "./components/Common_Elements/Spinner";

const About = lazy(() => import("./components/About/AboutPage"));
const Contact = lazy(() => import("./components/Contact/Contact"));

function App() {
  return (
    <Router>
      <div className="App">
        <Navbar />
        <Switch>
          <Suspense fallback={<Spinner />}>
            <Route exact path="/" component={Home} />
            <Route path="/about" component={About} />
            <Route path="/contact" component={Contact} />
          </Suspense>
        </Switch>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
