import React from "react";
import { NavLink } from "react-router-dom";
import Logo from "../../../assists/logo.png";
import "./navbar.css";

export default class Navbar extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isNavOpen: false
    };
  }

  toggleNav = () => {
    let screenWidth = window.innerWidth;
    if (screenWidth > 768) return;
    this.setState(prevState =>
      this.setState({ isNavOpen: !prevState.isNavOpen })
    );
  };

  render() {
    return (
      <div className="nav__container">
        <button onClick={this.toggleNav}>
          <img src={Logo} alt="Logo" />
        </button>

        <nav className={this.state.isNavOpen ? "open" : null}>
          <NavLink exact to="/" className="nav__navLink">
            Home
          </NavLink>
          <NavLink to="/about" className="nav__navLink">
            About
          </NavLink>
          <NavLink to="/contact" className="nav__navLink">
            contact
          </NavLink>
        </nav>
      </div>
    );
  }
}
