import React, { Component } from "react";
import Axios from "axios";
import ProjectCard from "./ProjectCard";

export default class ShowCase extends Component {
  constructor(props) {
    super(props);

    this.state = {
      projects: [],
      key: "14882572-5eace0f7f1898ce91731e6d64"
    };
  }

  async componentDidMount() {
    Axios.get(
      `https://pixabay.com/api/?key=${this.state.key}&q=design&image_type=photo&per_page=12`
    ).then(res => this.setState({ projects: res.data.hits }));
  }

  render() {
    return (
      <main style={{ display: "flex", flexWrap: "wrap" }}>
        {this.state.projects === [] ? (
          <h1>Loading...</h1>
        ) : (
          this.state.projects.map(project => (
            <ProjectCard
              key={project.id}
              imgURL={project.largeImageURL}
              likes={project.likes}
              views={project.views}
            />
          ))
        )}
      </main>
    );
  }
}
