import React from "react";
import Banner from "../Common_Elements/Banner/Banner";
import ShowCase from "./ShowCase/ShowCase";

export default function Home() {
  return (
    <div style={{ backgroundColor: "#f6f6f6" }}>
      <Banner
        title={"UI/UX & Graphic Designer"}
        content={`I am a Graphic & Web Designer based in New York, specializing in User
        Interface Design and Development.`}
        bgColor="transparent"
        contentColor="#787878"
      />
      <ShowCase />
    </div>
  );
}
