import React from "react";

const STYLES = {
  li: {
    marginBottom: 9
  },
  link: {
    fontSize: "0.8em"
  }
};

const FooterList = ({ items }) => (
  <ul className="footer__element footer__element-list">
    {items.map(item => (
      <li key={item.id} style={{ ...STYLES.li }}>
        <a
          className="footerLink"
          href="#"
          target="_blank"
          style={{ ...STYLES.link }}
        >
          {item.content}
        </a>
      </li>
    ))}
  </ul>
);

export default FooterList;
