import React from "react";
import "./banner.css";

const STYLES = {
  heading: {
    marginBottom: 30
  }
};

export default function Banner({
  title,
  content,
  bgColor,
  headingColor,
  contentColor,
  children
}) {
  return (
    <div className="banner" style={{ backgroundColor: bgColor }}>
      <h1 style={{ ...STYLES.heading, color: headingColor || "#333" }}>
        {title}
      </h1>
      <p style={{ color: contentColor, marginBottom: 20, lineHeight: 1.7 }}>
        {content}
      </p>
      {children}
    </div>
  );
}
