import React from "react";
import "./projectCard.css";

const STYLES = {};

export default function ProjectCard({ imgURL, views, likes }) {
  return (
    <div className="projectCard">
      <div className="statsDiv">
        <span className="projectCard__statHolder">
          <span>
            <i className="fa fa-eye" style={{ color: "#eee" }}></i>
          </span>
          <strong style={{ padding: "0 14px" }}>{views}</strong>
        </span>

        <span className="projectCard__statHolder">
          <span>
            <i className="fa fa-heart" style={{ color: "#ff3535" }}></i>
          </span>
          <strong style={{ padding: "0 14px" }}>{likes}</strong>
        </span>
      </div>
      <div
        className="projectCard__photo-holder"
        style={{ backgroundImage: `url(${imgURL})` }}
      />
    </div>
  );
}
